import Vue from 'vue'
import Router from 'vue-router'
import Homepage from '@/components/Homepage';
import Shipmentresult from '@/components/Shipmentresult';
import ShipmentSearch from '@/components/ShipmentSearch';

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      name: 'homepage',
      path: '/',
      component: Homepage
    },
    {
      name: 'shipmentresult',
      path: '/shipmentresult',
      component: Shipmentresult
    },
    {
      name: 'shipmentsearch',
      path: '/shipmentsearch',
      component: ShipmentSearch
    },
    {
      name: 'carriers',
      path: '/',
      component: Homepage
    },
    {
      name: 'clients',
      path: '/',
      component: Homepage
    },
    {
      name: 'premium',
      path: '/',
      component: Homepage
    }
  ],
  scrollBehavior(to, from, savedPosition) {
    return { x: 0, y: 0 };
  }
})



