<template>
  <div id="homepage" class="home-page d-flex flex-column justify-content-between">
    <div class="preview d-flex flex-column align-items-center justify-content-center">
      <div class="ship"></div>
      <h1 class="title">
        Быстро. Просто. Удобно.
      </h1>
      <h2 class="sub-title">Ищем маршрут до двери за несколько секунд. И это бесплатно.</h2>
      <div class="preview__wave"></div>
      <router-link class="common-button" :to="{name: 'shipmentsearch'}" tag="a">Считаем!</router-link>
    </div>
  </div>
</template>

<script>
  import '../assets/Homepage.css';
  import MainMenu from './MainMenu';
  export default {
    name: 'homepage',
    components: {
      MainMenu
    }
  }
</script>



